<template>
    <yd-layout class="monthSalary">
        <div class="salaryMain">
            <div class="salaryHeader" :class="{headerHight:salaryGroup.length<2}">
                <div class="ft">本月实际收入</div>
                <div class="tt" v-if="salaryFlag==false">****</div>
                <div class="tt" v-if="salaryFlag==true">¥{{sunAll}}</div>
                <div class="zgs">{{groupText}}</div>
                <div class="img" @click="passShow=true" v-if="salaryFlag==false">查看工资</div>
                <div class="img" @click="statusPopup=true" v-if="salaryFlag==true&&salaryGroup.length>1">切换工资组</div>
            </div>
            <div class="stitle" @click="open()">
                {{formatDate(defaultDate,'yyyy')}}年{{formatDate(defaultDate,'MM')}}月
            </div>
            <yd-datetime ref="datetime" class="monthDate" v-model="defaultDate" type="date" v-show="false" slot="right"></yd-datetime>
            <div class="salaryContent" v-if="salaryFlag==false">
                <yd-flexbox>
                    <yd-flexbox-item>应发工资</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight">****</yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="flexbox">
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor">基本工资</div>
                        <div class="itemNum">****</div>
                    </yd-flexbox-item>
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor itemText">绩效工资</div>
                        <div>****</div>
                    </yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="flexbox">
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor">职位工资</div>
                        <div class="itemNum">****</div>
                    </yd-flexbox-item>
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor itemText">书报节日费</div>
                        <div>****</div>
                    </yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="flexbox">
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor">总经理奖励</div>
                        <div class="itemNum">****</div>
                    </yd-flexbox-item>
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor itemText">年终奖预发</div>
                        <div>****</div>
                    </yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox>
                    <yd-flexbox-item>代扣费用</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight">****</yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="flexbox">
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor">个人社保</div>
                        <div class="itemNum">****</div>
                    </yd-flexbox-item>
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor itemText">个人公积金</div>
                        <div>****</div>
                    </yd-flexbox-item>
                </yd-flexbox>
                <yd-flexbox class="flexbox">
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor">补充公积金</div>
                        <div class="itemNum">****</div>
                    </yd-flexbox-item>
                    <yd-flexbox-item class="itemDiv">
                        <div class="itemClolor itemText">个人所得税</div>
                        <div>****</div>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
            <div class="salaryContent2" v-if="salaryFlag==true&&(yfJsonData.length!=0 || dkJsonData.length!=0)">
                <yd-flexbox v-if="Object.keys(yfJsonData).length!=0">
                    <yd-flexbox-item>应发工资</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="yfSalary"></yd-flexbox-item>
                </yd-flexbox>
                <ul>
                    <li v-for="(value,key,index) in yfJsonData" :key="index">
                        <span class="li_span01" v-text="key"></span>
                        <span class="li_span02" v-text="value"></span>
                    </li>
                </ul>
                <yd-flexbox v-if="Object.keys(dkJsonData).length!=0">
                    <yd-flexbox-item>代扣费用</yd-flexbox-item>
                    <yd-flexbox-item class="itemRight" v-text="dkCost"></yd-flexbox-item>
                </yd-flexbox>
                <ul>
                    <li v-for="(value,key,index) in dkJsonData" :key="index">
                        <span class="li_span01" v-text="key"></span>
                        <span class="li_span02" v-text="value"></span>
                    </li>
                </ul>
            </div>
        </div>
        <yd-popup v-model="passShow" position="center" width="6rem" height="5.5rem" class="ydPopup">
            <div class="passDiv">
                <div class="passwordTitle">请输入人力资源系统薪酬查询密码：</div>
                <div>
                    <yd-input slot="right" type="password" v-model="password" placeholder="请输入密码"></yd-input>
                </div>
                <div class="passBottom">
                    <div class="passComf1" @click="queryBtn(password)">确认</div>
                </div>
                <div class="explain">说明：若需要修改密码，请登录人力资源系统PC端进行修改</div>
            </div>
            <div class="return" @click="passShow=false">
                <img src="../../../static/images/close@2x.png">
            </div>
        </yd-popup>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" @click="statusPopup=false">取消</div>
                <div class="rightBtn" @click="statusPopup=false;groupValue=groupValue1">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li v-for="(item,index) in salaryGroup" :key="index" class="li" @click="statusNum=index;groupText=item.text;groupValue1=item.value" :class="{active:statusNum==index}" v-text="item.text"></li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" bgcolor="#F4F4F2" activeColor="#369df4" color="#999999">
            <yd-tabbar-item link="" title="月度薪酬" active>
                <yd-icon name="month1" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
            <yd-tabbar-item link="/yearSalary" title="年度薪酬">
                <yd-icon name="year1" slot="icon" size="0.54rem"></yd-icon>
            </yd-tabbar-item>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import md5 from 'md5';
export default {
    name: 'monthSalary',
    data() {
        return {
            defaultDate: '',
            tabTxt: '查看工资',
            passShow: false,
            password: '',
            salaryGroup: [],
            sGroupFlag: false,
            dkJsonData: [],
            yfJsonData: [],
            salaryFlag: false,
            statusPopup: false,
            statusNum: 0,
            groupText: '',
            groupValue: '',
            groupValue1: '',
            yfSalary: 0,
            dkCost: 0,
            sunAll: '****',
            dateNum: 0
        }
    },
    mounted() {
        document.setTitle('薪酬管理');
        let vm = this;
        let currentdate = vm.formatDate(new Date(), 'yyyy-MM-dd');
        vm.defaultDate = currentdate;
        let pscookie = vm.getCookieValue('salary');
        if (pscookie != '') {
            vm.salaryFlag = true;
            vm.getSalaryGroup(pscookie);
        } else {
            vm.salaryFlag = false;
        }
    },
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    watch: {
        defaultDate: function() {
            let vm = this;
            if (vm.salaryFlag == true) {
                let pscookie = vm.getCookieValue('salary');
                if (pscookie != '') {
                    vm.CheckWagesByMonth(vm.defaultDate, vm.groupValue, pscookie);
                } else {
                    vm.passShow = true;
                }
            }
        },
        groupValue: function() {
            let vm = this;
            if (vm.salaryFlag == true) {
                let pscookie = vm.getCookieValue('salary');
                if (pscookie != '') {
                    vm.CheckWagesByMonth(vm.defaultDate, vm.groupValue, pscookie);
                } else {
                    vm.passShow = true;
                }
            }
        }
    },
    methods: {
        CheckWagesByMonth(yearMon, salaryGroup, password) {
            let vm = this;
            vm.dkCost = 0;
            vm.yfSalary = 0;
            let date = vm.formatDate(yearMon, 'yyyy') + vm.formatDate(yearMon, 'MM');
            if (password == '') {
                vm.passShow = true;
            } else {
                vm.post(vm.conf.api + '/CheckWages/CheckWagesByMonth', {
                    token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    yearMon: date,
                    salaryGroup: salaryGroup,
                    password: password,
                    empcode: vm.getUserInfo.empcode
                }, function(res) {
                    if (res.isSuccess == 'true' || res.isSuccess == true) {
                        vm.passShow = false;
                        vm.tabTxt = '切换工资组';
                        vm.salaryFlag = true;
                        vm.password = '';
                        vm.dkJsonData = res.jsonData.DK;
                        vm.yfJsonData = res.jsonData.YF;
                        vm.dkCost = res.jsonData.sum_DK;
                        vm.yfSalary = res.jsonData.sum_YF;
                        vm.sunAll = res.jsonData.sum_ALL;
                    } else {
                        vm.deleteCookie('salary')
                        // vm.passShow = false;
                        vm.$dialog.toast({
                            mes: res.errorMessage,
                            timeout: 2000
                        });
                    }
                }, function(res) {
                    vm.deleteCookie('salary');
                });
            }
        },
        getSalaryGroup(password) {
            let vm = this;
            var promise = new Promise(function(resolve, reject) {
                vm.post(vm.conf.api + '/CheckWages/getSalaryGroup', {
                    token: `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    empcode: vm.getUserInfo.empcode,
                    password: password
                }, function(res) {
                    if (res.isSuccess == 'true' || res.isSuccess == true) {
                        vm.salaryGroup = res.jsonData;
                        if (res.jsonData.length != 0) {
                            vm.groupText = res.jsonData[0].text;
                            vm.groupValue = res.jsonData[0].value;
                        }
                        resolve(true);
                    } else {
                        vm.deleteCookie('salary')
                        // vm.passShow = false;
                        vm.$dialog.toast({
                            mes: res.errorMessage,
                            timeout: 2000
                        });
                    }
                }, function(res) {
                    vm.deleteCookie('salary');
                });
            });
            promise.then(function(data) {
                if (data) {
                    vm.sGroupFlag = true;
                    vm.CheckWagesByMonth(vm.defaultDate, vm.groupValue, password);
                }
            })
        },
        addCookie(name, value, minute) {
            var _name = escape(name);
            var _value = escape(md5(value));
            var expires = new Date();
            expires.setTime(expires.getTime() + minute * 60 * 1000);
            var _expires = (typeof minute) == 'string' ? '' : ';expires=' + expires.toUTCString();
            document.cookie = _name + '=' + _value + _expires;
        },
        getCookieValue(name) {
            var _name = escape(name);
            var allcookies = document.cookie;
            _name += '=';
            var pos = allcookies.indexOf(_name);
            if (pos != -1) {
                var start = pos + _name.length;
                var end = allcookies.indexOf(';', start);
                if (end == -1) end = allcookies.length;
                var value = allcookies.substring(start, end);
                return (value);
            } else {
                return '';
            }
        },
        deleteCookie(name) {
            var _name = escape(name);
            var _expires = new Date(0);
            document.cookie = _name + '=' + ';expires=' + _expires.toUTCString();
        },
        queryBtn(password) {
            let vm = this;
            vm.addCookie('salary', password, 2);
            // vm.getCookieValue('salary');
            if (password == '') {
                vm.$dialog.toast({
                    mes: '请输入密码',
                    timeout: 2000
                });
            } else if (vm.salaryGroup.length == 0) {
                vm.getSalaryGroup(md5(password));
                vm.password = '';
            } else {
                vm.CheckWagesByMonth(vm.defaultDate, vm.groupValue, md5(password));
            }
        },
        open() {
            this.$refs.datetime.open();
        }
    }
}
</script>
<style lang="less">
@import './salary.less';

.monthSalary {
    .passDiv {
        background-color: #fff;
        padding: 0 .4rem;
        border-radius: 5px;
    }

    .passwordTitle {
        height: 1.1rem;
        line-height: 1.1rem;
        font-size: .32rem;
        color: #333;
        text-align: center;
    }

    .explain {
        font-size: .24rem;
        color: #666;
        margin-top: .5rem;
        padding-bottom: .6rem;
    }

    .passBottom {
        width: 100%;
        height: .43rem;
        background-color: #fff;
        margin-top: .4rem;
        // padding-bottom: .2rem;
    }

    .return {
        text-align: center;
        width: .74rem;
        border-radius: 50%;
        background: #bababa;
        height: .75rem;
        margin: 0 auto;
        margin-top: .2rem;
        padding-top: .23rem;

        img {
            width: .3rem;
            height: .3rem;
        }
    }

    .yd-input {
        height: .6rem;
        background-color: #f4f4f4;
        padding-left: .18rem;
        border-radius: .07rem;
    }

    .yd-popup-center {
        overflow: hidden;
        background-color: initial !important;
        border-radius: 5px;
        // padding: 0 .4rem;
    }
}

.yd-icon-month1:before,
.yd-icon-year1:before {
    display: block;
    content: "";
}

.yd-icon-month1:before {
    width: 0.5rem;
    height: 0.5rem;
    background: url(../../../static/images/salary/monthSalary@2x.png) no-repeat 0 0;
    background-size: 0.5rem;
}

.yd-icon-year1:before {
    width: 0.5rem;
    height: 0.5rem;
    background: url(../../../static/images/salary/yearSalary1@2x.png) no-repeat 0 0;
    background-size: 0.5rem;
}

.passComf1 {
    height: .43rem;
    width: 1.65rem;
    line-height: .43rem;
    text-align: center;
    font-size: .24rem;
    background-color: #59a5fd;
    color: #fff;
    margin: 0 auto;
    border-radius: 3px;
}

.monthDate {
    background: url(../../../static/images/performance/jt1@3x.png) no-repeat;
    background-size: .2rem;
    background-position: 2rem center;
    height: .98rem;
    background-color: #fff;
    font-size: .32rem;
    color: #333;
    padding-left: .52rem;
    line-height: .98rem;
}

.yd-datetime-item:nth-child(3) {
    display: none;
}

.headerHight {
    height: 3.4rem !important;
}
</style>


